import React from 'react'

export default class LazyLoadSearch extends React.Component {
  constructor () {
    super()
    this.state = {
      visiable: false,
      value: ''
    }
    this.focusEven = this.focusEven.bind(this)
    this.blurEvent = this.blurEvent.bind(this)
  }

  focusEven () {
    this.setState({
      visiable: true
    })
  }

  blurEvent () {
    // setTimeout(() => {
    //   this.setState({
    //     visiable: false
    //   })
    // }, 0)
  }

  optionClick (e) {
    console.info(e)
  }
  render () {
    const { visiable, value } = this.state
    return (
      <div className='lazy-select'>
        <div>
          <input
            type='text'
            onFocus={this.focusEven}
            onBlur={this.blurEvent}
            value={value}
          />
        </div>
        <div className='lazy-layers' style={{ display: visiable ? 'block' : 'none' }}>
          <ul >
            <li onClick={this.optionClick}>text01</li>
            <li>text02</li>
            <li>text03</li>
            <li>text04</li>
          </ul>
        </div>
      </div>
    )
  }
}
